import React, { Component } from 'react';
import logo from '../../logo.svg';
import '../../css/styles.css';
import icons from '../../vendors/feather/feather-sprite.svg';

class Sidebar extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className='sidebar'>
        
      </div>
    );
  }
}

class Header extends Component {
  constructor(props) {
    super(props);
  }
  
  render() {
    return (
      <header className='header'>
        <h1 className="header__title">Makerbook</h1>
      </header>
    )
  }
}

function TileIcon(props) {
  return (
    <svg className={`tile__icon feather feather-${props.name}`}
    xmlns= "http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox='0 0 24 24'
    width="24"
    height="24"
    fill="none"
    stroke="currentColor"
    stroke-width="2"
    stroke-linecap="round"
    stroke-linejoin="round">
      <use xlinkHref={`${icons}#${props.name}`} />
    </svg>
  )
}

class Tile extends Component {
  constructor(props) {
    super(props);
  }
  
  render() {
    return (
      <div className={'tile ' + this.props.tileClass}>
        <a className='tile__link' href={this.props.tileURL}>
          <img className='tile__img' src={this.props.tileImg} />
          <TileIcon className="tile__icon" name={this.props.tileIcon} />
          <span className='tile__name'>{this.props.tileName}</span>
        </a>
      </div>
    );
  }
}

class Grid extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: ''
    }
  }
  
  render() {
    return (
      <div className ="grid">
      <Tile tileClass="category" tileName='Photography' tileURL='' tileIcon='' tileIcon='camera' />
      <Tile tileClass="category" tileName='Graphics' tileURL='' tileImg='' tileIcon='image' />
      <Tile tileClass="category" tileName='Textures' tileURL='' tileImg='' tileIcon='grid' />
      <Tile tileClass="category" tileName='Typography' tileURL='' tileImg='' tileIcon='type' />
      <Tile tileClass="category" tileName='Colors' tileURL='' tileImg='' tileIcon='droplet' />
      <Tile tileClass="category" tileName='UI/UX' tileURL='' tileImg='' tileIcon='user' />
        </div>
    )
  }
}

class App extends Component {
  render() {
    return (
      <div className="app">
        <Header />
        <Grid />
        </div>
      )
  }
}

export default App;



